import styled from "styled-components";

export const MainContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  text-align: left;
  cursor: default;
`;

export const BookContainer = styled.div`
  background-color: #f3f2f1;
  width: 280px;
  height: 400px;
  margin: 20px;
`;

export const ImageContainer = styled.div`
  & > * {
    height: 200px;
    width: 140px;
    box-shadow: 11px 11px 5px rgba(0, 0, 0, 0.21);

    &:hover {
      -webkit-transition: all 0.7s ease;
      -moz-transition: all 0.7s ease;
      -o-transition: all 0.7s ease;
      transition: all 0.7s ease;

      cursor: pointer;
      height: 220px;
      width: 154px;
      box-shadow: 17px 17px 5px rgba(0, 0, 0, 0.21);
      transform: skewY(-0.9deg);
    }
  }
  height: 205px;
  margin: 10% auto;
  text-align: center;
`;

export const Category = styled.div`
  color: #888888;
  text-decoration: underline;
  padding-left: 13px;
  height: 21px;

  cursor: default;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
`;

export const Title = styled.h1`
  font-size: large;
  font-weight: 700;
  padding: 13px;
  position: relative;
  top: -20px;

  cursor: pointer;

  // Обрезка длинного текста
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  height: 2.9em;

  &:hover {
    -webkit-transition: all 0.3s ease-in;
    -moz-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;

    text-shadow: 1.3px 1.3px 1px rgba(70, 70, 70, 0.31);
  }
`;

export const Author = styled.div`
  font-style: italic;
  color: #888888;
  position: relative;
  bottom: 15px;
  left: 15px;
  width: 250px;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
`;

export const TotalItemsPar = styled.p`
  text-align: center;
  margin: 20px 0;
  font-weight: 400;
  font-size: 1.3rem;
`;

export const LoadMore = styled.button`
  width: 80px;
  height: 35px;
  text-align: center;
  cursor: pointer;
  border: 1px solid #000;
  background-color: white;
  border-radius: 0px;
  font-family: "Raleway", sans-serif;
  background-color: white;
  margin: 60px;
`;

export const Center = styled.div`
  padding: 20px 0;
  text-align: center;
`;
