import React, { useState } from "react";
import { Categories } from "./components/HeaderComponent/Categories";
import { Sorting } from "./components/HeaderComponent/Sorting";
import { SearchBar } from "./components/HeaderComponent/SearchBar";
import { Volumes } from "./components/MainComponent/Volumes/Volumes";
import {
  Form,
  HeaderMainContainer,
  SelectsContainer,
} from "./components/HeaderComponent/styles";
import { VolumeInfo } from "./components/VolumeInfo/VolumeInfo";
import { getVolumeIdSelector } from "./store/selectors";
import { useSelector } from "react-redux";
import { RootState } from "./types";

function App() {
  const volumeId = useSelector<RootState, RootState["id"]>(getVolumeIdSelector);

  return (
    <div className="App">
      <HeaderMainContainer>
        Search for books
        <Form>
          <SearchBar />
          <SelectsContainer>
            <Categories />
            <Sorting />
          </SelectsContainer>
        </Form>
      </HeaderMainContainer>
      {volumeId ? <VolumeInfo /> : null}
      <Volumes />
    </div>
  );
}

export default App;
