import {
  Action,
  SELECT_CATEGORY,
  SELECT_SORTING_TYPE,
  SEARCH_VALUE,
  GET_VOLUMES,
  SET_TOTAL_ITEMS,
  GET_MORE_VOLUMES,
  SET_VOLUME_ID,
  SET_LOADING,
  SET_MESSAGE,
} from "./actions";
import { RootState } from "../types";

const initialState: RootState = {
  category: "All",
  sortingBy: "relevance",
  searchValue: "",
  volumes: [],
  totalVolumes: null,
  id: null,
  loading: false,
  message: null,
};

export const rootReducer = (
  state: RootState = initialState,
  action: Action
) => {
  switch (action.type) {
    case SELECT_CATEGORY: {
      return {
        ...state,
        category: action.payload,
      };
    }
    case SELECT_SORTING_TYPE: {
      return {
        ...state,
        sortingBy: action.payload,
      };
    }
    case SEARCH_VALUE: {
      return {
        ...state,
        searchValue: action.payload,
      };
    }
    case GET_VOLUMES: {
      return {
        ...state,
        volumes: [...action.payload],
      };
    }
    case GET_MORE_VOLUMES: {
      return {
        ...state,
        volumes: [...state.volumes, ...action.payload],
      };
    }
    case SET_TOTAL_ITEMS: {
      return {
        ...state,
        totalVolumes: action.payload,
      };
    }
    case SET_VOLUME_ID: {
      return {
        ...state,
        id: action.payload,
      };
    }
    case SET_LOADING: {
      return { ...state, loading: action.payload };
    }
    case SET_MESSAGE: {
      return {
        ...state,
        message: action.payload,
      };
    }
    default:
      return state;
  }
};
