import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootState, volumeType } from "../../../types";
import {
  getVolumesSelector,
  getTotalItemsSelector,
  searchValueSelector,
  categorySelector,
  sortingBySelector,
  getLoadingSelector,
  getMessageSelector,
} from "../../../store/selectors";
import {
  MainContainer,
  BookContainer,
  ImageContainer,
  Category,
  Author,
  Title,
  TotalItemsPar,
  LoadMore,
  Center,
} from "./styles";
import {
  getMoreVolumes as dispatchMoreVolumes,
  setTotalItems as dispatchTotalItems,
  setVolumeId as dispatchVolumeId,
  setLoading as dispatchLoading,
} from "../../../store/actions";
import { mapArray } from "../../../services/utils";
import { Loader } from "../Loader/Loader";

export const Volumes = () => {
  const dispatch = useDispatch();

  const volumes = useSelector<RootState, RootState["volumes"]>(
    getVolumesSelector
  );
  const totalItems = useSelector<RootState, RootState["totalVolumes"]>(
    getTotalItemsSelector
  );
  const searchValue = useSelector<RootState, RootState["searchValue"]>(
    searchValueSelector
  );
  const category = useSelector<RootState, RootState["category"]>(
    categorySelector
  );
  const sortingBy = useSelector<RootState, RootState["sortingBy"]>(
    sortingBySelector
  );
  const loading = useSelector<RootState, RootState["loading"]>(
    getLoadingSelector
  );
  const message = useSelector<RootState, RootState["message"]>(
    getMessageSelector
  );

  let displayedVolumes = volumes.map((item: volumeType) => {
    const authors = item.authors !== null ? item.authors : null;

    const ActiveCategory =
      item.category !== null && category === "All"
        ? item.category[0]
        : category;

    return (
      <BookContainer
        key={item.id}
        onClick={() => dispatch(dispatchVolumeId(item.id))}
      >
        <ImageContainer>
          <img src={item.cover} />
        </ImageContainer>
        <Category>{ActiveCategory}</Category>
        <Title>{item.title}</Title>
        <Author>{authors}</Author>
      </BookContainer>
    );
  });

  const findMoreVolumes = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();

    try {
      dispatch(dispatchLoading(true));
      fetch(
        `https://www.googleapis.com/books/v1/volumes?q=${searchValue}:${category}&startIndex=${volumes.length}&maxResults=30&orderBy=${sortingBy}&key=AIzaSyDkzWEuhuu1tbm0bbIFQ9tm155FCfCUiLI`
      )
        .then((response) => response.json())
        .then((result) => {
          dispatch(dispatchTotalItems(result.totalItems));
          mapArray(result.items).then((result) =>
            dispatch(dispatchMoreVolumes(result))
          );
        })
        .then(() => dispatch(dispatchLoading(false)));
    } catch (e) {
      dispatch(dispatchLoading(false));
      alert("Sorry, there is an error in the data, please try again");
      alert((e as Error).name);
      alert((e as Error).message);
    }
  };

  return (
    <Center>
      {loading === false || volumes.length >= 30 ? (
        <>
          <TotalItemsPar>
            {totalItems ? `Found ${totalItems} results` : null}
          </TotalItemsPar>
          <MainContainer>{displayedVolumes}</MainContainer>
          {totalItems && totalItems > volumes.length ? (
            loading ? (
              <Loader loaderSize="35px" offset="50px" borderSize="8px" />
            ) : (
              <LoadMore onClick={findMoreVolumes}>Load more</LoadMore>
            )
          ) : null}
          {message ? <p>{message}</p> : null}
          {totalItems ? <p>ALEXANDER KOMKOV</p> : null}
        </>
      ) : (
        <Loader loaderSize="80px" offset="90px" borderSize="13px" />
      )}
    </Center>
  );
};
