import React, { useState } from "react";
import { InputContainer, Button, SearchBarContainer } from "./styles";
import {
  setSearchValue as dispatchSearchValue,
  getVolumes as dispatchVolumes,
  setTotalItems as dispatchTotalItems,
  setLoading as dispatchLoading,
  setMessage as dispatchMessage,
} from "../../store/actions";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../types";
import { sortingBySelector, categorySelector } from "../../store/selectors";
import { mapArray } from "../../services/utils";

export const SearchBar = () => {
  const [searchValue, setSearchValue] = useState("");
  const dispatch = useDispatch();

  const onKeyDown = (event: React.KeyboardEvent<HTMLDivElement>): void => {
    if (event.key === "Enter") {
      event.preventDefault();
      event.stopPropagation();
      findVolumes(event);
    }
  };

  const sortingBy = useSelector<RootState, RootState["sortingBy"]>(
    sortingBySelector
  );
  const category = useSelector<RootState, RootState["category"]>(
    categorySelector
  );

  const findVolumes = (e: any) => {
    e.preventDefault();

    if (searchValue !== "") {
      dispatch(dispatchSearchValue(searchValue));
      setSearchValue("");

      try {
        dispatch(dispatchVolumes([]));
        dispatch(dispatchLoading(true));
        fetch(
          `https://www.googleapis.com/books/v1/volumes?q=${searchValue}:${category}&maxResults=30&orderBy=${sortingBy}&key=AIzaSyDkzWEuhuu1tbm0bbIFQ9tm155FCfCUiLI`
        )
          .then((response) => response.json())
          .then((result) => {
            if (result.totalItems === 0) {
              dispatch(dispatchTotalItems(null));
              dispatch(
                dispatchMessage("No results were found for your search.")
              );
              dispatch(dispatchLoading(false));
            } else {
              dispatch(dispatchMessage(null));
              dispatch(dispatchTotalItems(result.totalItems));
              mapArray(result.items)
                .then((result) => dispatch(dispatchVolumes(result)))
                .then(() => dispatch(dispatchLoading(false)));
            }
          });
      } catch (err: any) {
        dispatch(dispatchLoading(false));
        alert("Sorry, there is an error in the data, please try again");
        alert(e.name);
        alert(e.message);
      }
    }
  };

  return (
    <SearchBarContainer>
      <InputContainer
        onKeyDown={onKeyDown}
        placeholder="Enter the title of the book"
        value={searchValue}
        onChange={(e) => setSearchValue(e.target.value)}
      />
      <Button onClick={findVolumes}>Search</Button>
    </SearchBarContainer>
  );
};
