export const mapArray = async (oldArray: any[] | undefined) => {
  if (oldArray === undefined) {
    return [];
  }
  const newArray = await oldArray.map((item) => {
    // Проверка наличия данных

    const category = item.volumeInfo.categories
      ? [...item.volumeInfo.categories]
      : null;
    let authors = item.volumeInfo.authors ? [...item.volumeInfo.authors] : null;

    const cover =
      item.volumeInfo.imageLinks && item.volumeInfo.imageLinks.thumbnail
        ? item.volumeInfo.imageLinks.thumbnail
        : null;
    const description = item.volumeInfo.description
      ? item.volumeInfo.description
      : "No description...";

    // Создаем обьект книги
    const editArray = {
      id: item.id,
      title: item.volumeInfo.title,
      category: category,
      authors: authors,
      cover: cover,
      description: description,
    };
    return editArray;
  });
  return newArray;
};

