import styled, { keyframes } from "styled-components";

const loaderSpin = keyframes`
    from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`;

interface Props {
  loaderSize: string;
  offset: string;
  borderSize: string;
}

export const Loader = styled.div<Props>`
  margin: ${(props) => props.offset} 0px;

  display: inline-block;
  border: ${(props) => props.borderSize} solid rgba(0, 0, 0, 0.1);
  border-left-color: #888888;
  border-radius: 50%;
  width: ${(props) => props.loaderSize};
  height: ${(props) => props.loaderSize};
  -webkit-animation: ${loaderSpin} 1.3s ease infinite;
  animation: ${loaderSpin} 1.3s ease infinite;
`;
