export const SELECT_CATEGORY = "SELECT_CATEGORY";
export const SELECT_SORTING_TYPE = "SELECT_SORTING_TYPE";
export const SEARCH_VALUE = "SEARCH_VALUE";
export const GET_VOLUMES = "GET_VOLUMES";
export const SET_TOTAL_ITEMS = "SET_TOTAL_ITEMS";
export const GET_MORE_VOLUMES = "GET_MORE_VOLUMES";
export const SET_VOLUME_ID = "SET_VOLUME_ID";
export const SET_LOADING = "SET_LOADING";
export const SET_MESSAGE = "SET_MESSAGE";

export type Action =
  | {
      type: typeof SELECT_CATEGORY;
      payload: string;
    }
  | {
      type: typeof SELECT_SORTING_TYPE;
      payload: string;
    }
  | {
      type: typeof SEARCH_VALUE;
      payload: string;
    }
  | {
      type: typeof GET_VOLUMES;
      payload: any;
    }
  | { type: typeof SET_TOTAL_ITEMS; payload: number | null }
  | {
      type: typeof GET_MORE_VOLUMES;
      payload: any;
    }
  | {
      type: typeof SET_VOLUME_ID;
      payload: string;
    }
  | {
      type: typeof SET_LOADING;
      payload: boolean;
    }
  | {
      type: typeof SET_MESSAGE;
      payload: string | null;
    };

export const selectCategory = (category: string) => {
  return { type: SELECT_CATEGORY, payload: category };
};

export const selectSortingType = (type: string) => {
  return { type: SELECT_SORTING_TYPE, payload: type };
};
export const setSearchValue = (value: string) => {
  return { type: SEARCH_VALUE, payload: value };
};

export const getVolumes = (volumes: any) => {
  return { type: GET_VOLUMES, payload: volumes };
};

export const getMoreVolumes = (volumes: any) => {
  return { type: GET_MORE_VOLUMES, payload: volumes };
};

export const setTotalItems = (items: number | null) => {
  return { type: SET_TOTAL_ITEMS, payload: items };
};

export const setVolumeId = (id: string | null) => {
  return { type: SET_VOLUME_ID, payload: id };
};

export const setLoading = (loading: boolean) => {
  return { type: SET_LOADING, payload: loading };
};

export const setMessage = (message: string | null) => {
  return { type: SET_MESSAGE, payload: message };
};
