import styled from "styled-components";

const size = {
  mobileS: "320px",
  mobileM: "375px",
  mobileL: "425px",
  tablet: "768px",
  laptop: "1024px",
  laptopL: "1440px",
  desktop: "2560px",
};

export const device = {
  mobileS: `(min-width: ${size.mobileS})`,
  mobileM: `(min-width: ${size.mobileM})`,
  mobileL: `(min-width: ${size.mobileL})`,
  tablet: `(min-width: ${size.tablet})`,
  laptop: `(min-width: ${size.laptop})`,
  laptopL: `(min-width: ${size.laptopL})`,
  desktop: `(min-width: ${size.desktop})`,
  desktopL: `(min-width: ${size.desktop})`,
};

export const Modal = styled.div`
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 99;
  width: 100%;
  max-width: 1920px;
  height: 100%;
  max-height: 1080px;
`;

export const ModalContent = styled.div`
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  @media ${device.mobileS} {
    font-size: calc(9px + 6 * ((100vw - 320px) / 680));
    flex-direction: row;
  }

  @media ${device.tablet} {
    flex-direction: row;
  }

  height: 100%;
`;

export const ModalTitle = styled.h1`
  width: 90%;
  font-size: calc(25px + 6 * ((100vw - 320px) / 680));
`;

export const ModalBodyContainer = styled.div`
  width: 90%;
  height: 90%;
`;

export const ModalBody = styled.div`
  margin: 12% 5%;
  width: 90%;
  height: 100%;
  @media ${device.mobileS} {
  }

  @media ${device.laptop} {
  }

  cursor: default;
`;

export const Button = styled.button`
  @media ${device.mobileS} {
    width: calc(80px * 0.5);
    height: calc(34px * 0.5);
    font-size: calc(9px + 6 * ((100vw - 320px) / 680));
  }

  @media ${device.tablet} {
    width: calc(80px * 0.7);
    height: calc(34px * 0.7);
  }

  @media ${device.laptop} {
    width: 80px;
    height: 34px;
  }

  text-align: center;
  cursor: pointer;
  border: 1px solid #000;
  background-color: white;
  border-radius: 0px;
  font-family: "Raleway", sans-serif;
  background-color: white;
  @media ${device.mobileS} {
    font-size: calc(6px + 6 * ((100vw - 320px) / 680));
  }
`;

export const ModalImage = styled.div`
  margin: 0;
  padding: 0;
  width: 30%;
  height: 100%;
  background-color: #f3f2f1;
  text-align: center;
  position: relative;
  & > * {
    height: auto;
    max-width: 280px;
    width: 75%;
    box-shadow: 11px 11px 5px rgba(0, 0, 0, 0.21);
    position: absolute;
    margin: auto;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
  }
`;

export const ModalCategory = styled.p`
  margin-bottom: -10px;
`;

export const ModalAuthor = styled.p`
  color: #888888;
  text-decoration: underline;
`;
export const ModalDescription = styled.p`
  @media ${device.mobileS} {
    height: 50%;
  }
  @media ${device.tablet} {
    height: 35%;
  }
  width: 90%;
  height: 50%;
  max-height: 500px;
  padding: 10px 10px;
  border: 1px solid rgba(100, 100, 100, 0.2);
  overflow: scroll;
  display: -webkit-box;
  -webkit-box-orient: vertical;
`;
