import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Modal,
  ModalContent,
  ModalTitle,
  ModalBody,
  Button,
  ModalImage,
  ModalCategory,
  ModalAuthor,
  ModalDescription,
  ModalBodyContainer,
} from "./styles";
import { getVolumesSelector, getVolumeIdSelector } from "../../store/selectors";
import { RootState, volumeType } from "../../types";
import { setVolumeId } from "../../store/actions";

export const VolumeInfo = () => {
  const dispatch = useDispatch();

  const volumes = useSelector<RootState, RootState["volumes"]>(
    getVolumesSelector
  );
  const volumeInfoId = useSelector<RootState, RootState["id"]>(
    getVolumeIdSelector
  );

  const findVolume = (item: volumeType) => {
    if (item.id === volumeInfoId) {
      return item;
    }
  };
  const volumeInfo = volumes.find(findVolume)!;

  const onKeyDown = (event: React.KeyboardEvent<HTMLDivElement>): void => {
    if (event.key === "Escape") {
      event.preventDefault();
      event.stopPropagation();
      dispatch(setVolumeId(null));
    }
  };

  return (
    <Modal>
      <ModalContent onKeyDown={onKeyDown} tabIndex={-1}>
        <ModalImage>
          <img src={volumeInfo.cover} />
        </ModalImage>
        <ModalBodyContainer>
          <ModalBody>
            <ModalCategory>
              {volumeInfo.category ? volumeInfo.category.join(" / ") : null}
            </ModalCategory>
            <ModalTitle>{volumeInfo.title}</ModalTitle>
            <ModalAuthor>
              {volumeInfo.authors ? volumeInfo.authors.join(", ") : null}
            </ModalAuthor>
            <ModalDescription>{volumeInfo.description}</ModalDescription>
            <Button onClick={() => dispatch(setVolumeId(null))}>Close</Button>
          </ModalBody>
        </ModalBodyContainer>
      </ModalContent>
    </Modal>
  );
};
