import React from "react";
import { useDispatch } from "react-redux";
import { selectSortingType as dispatchSortingType } from "../../store/actions";
import { SelectContainer } from "./styles";

export const Sorting = () => {
  const dispatch = useDispatch();

  const getValue = (selectCategory: HTMLSelectElement) => {
    const category = selectCategory.value;
    dispatch(dispatchSortingType(category));
  };

  return (
    <div>
      Sorting by <SelectContainer id="categories" onChange={(e) => getValue(e.target)}>
        <option value="relevance" selected>
          relevance
        </option>
        <option value="newest">newest</option>
      </SelectContainer>
    </div>
  );
};
